                        // toggle
                        .toggle {
                            position: fixed;
                            width: 50px;
                            height: 50px;
                            top: 30px;
                            right: 35px;
                            cursor: pointer;
                            background-color: $color-primary-white;
                            border-radius: 50%;
                            span {
                                position: absolute;
                                width: 50%;
                                height: 2px;
                                @extend %center;
                                background-color: $color-black-dark;
                                display: block;
                                &:after,
                                &::before {
                                    position: absolute;
                                    width: 100%;
                                    height: 100%;
                                    content: "";
                                    @extend %center;
                                    background-color: $color-black-dark;
                                    @extend %transition;
                                }
                                &:after {
                                    top: 10px;
                                }
                                &::before {
                                    top: -10px;
                                }
                            }
