                            @include fonts-style(18px);
                        }
                    }
                    @media(max-width: 767px) {
                        & {
                            display: none;
                        }
                    }
                }
                
                // contact
                .contact-us {
                    background-image: linear-gradient(to right bottom, #46ce59, #00b176);
                    padding: 100px 0px;
                    .contact {
                        background-image: url('../images/15.png');
                        background-size: cover;
                        height: 450px;
                        @include width(70%);
                        margin: auto;
                        .form {
                            position: relative;
                            width: 60%;
                            height: 100%;
                            overflow: hidden;
                            z-index: 1;
                            &::before {
                                content: '';
                                position: absolute;
                                left: -70px;
                                top: 0;
                                width: 100%;
                                height: 100%;
                                background: #fff;
                                opacity: .9;
                                transform: skewX(-20deg);
                                transform-origin: left;
                                z-index: -1;
                            }
                            h2 {
                                margin: 20px 40px 50px;
                            }
                            input[type='text'],
                            input[type='email'] {
                                width: 60%;
                                margin: 20px 40px;
                                display: block;
                                height: 40px;
                                background-color: #fff;
                                color: #999;
                                border: none;
                                padding: 20px;
                                &::placeholder {
                                    color: #ccc;
                                    font-weight: 400;
                                }
                                &:focus {
                                    border: none;
                                    outline: none;
                                }
                            }
                            input[type=radio] {
                                display: none;
                            }
                            .radio {
                                margin: 30px 0 0 50px;
                                position: relative;
                                font-size: 18px;
                                color: #ccc;
                                font-weight: 400;
                                line-height: 1.5;
                                @include font-family('Nexa-light.otf');
                                cursor: pointer;
                            }
                            & span {
                                position: absolute;
                                left: -30px;
                                top: 5px;
                                width: 20px;
                                height: 20px;
                                border-radius: 50%;
                                border: 3px solid $color-primary-dark;
                                &::after {
                                    content: '';
                                    position: absolute;
                                    left: 50%;
                                    top: 50%;
                                    width: 7px;
                                    height: 7px;
                                    border-radius: 50%;
                                    transform: translate(-50%, -50%);
                                    opacity: 0;
                                    background-color: $color-primary-dark;
                                    transition: all .1s ease-in-out;
                                    display: block;
                                }
                            }
                        }
                        & input[type=radio]:checked~span::after {
                            opacity: 1;
                        }
                        button {
                            @extend %button;
                            display: block;
                            margin: 40px;
                        }
                    }
                    // @media(max-width:1200px)
                    @media(max-width:1200px) {
                        .contact {
                            @include width(90%!important);
                        }
                        h2 {
                            @include m-bottom(40px)
                        }
                    }
                    @media(max-width:767px) {
                        .contact .form {
                            width: 100%;
                            &::before {
                                transform: skewX(0deg)!important;
                                left: 0;
                            }
                            & h2 {
                                margin: 20px;
                            }
                            & input[type='text'],
                            & input[type='email'] {
                                width: 90%;
                                margin: 20px auto;
                            }
                        }
                    }
                }
                
                // contact
                // footer
                footer {
                    background-color: $color-black-dark;
                    padding: 60px 0px;
                    .col-md-12 {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        @include m-bottom(80px)
                    }
                    .col-md-5 {
                        @include m-left(4%);
                        margin-right: 4%;
                        padding: 0;
                        &::before {
                            content: '';
                            position: absolute;
                            top: -15px;
                            left: 0;
                            width: 100%;
                            height: 1px;
                            background-color: $color-greey-dark;
                        }
                        .links {
                            display: flex;
                            align-items: flex-start;
                            justify-content: space-between;
                            @extend %uppercase;
                            a {
                                color: $color-primary-white;
                                @extend %link;
                                @extend %transition;
                                padding-top: 10px;
                                font-size: 16px;
                                &:hover {
                                    color: $color-primary;
                                }
                            }
                        }
                        p {
                            font-size: 16px;
                            color: $color-primary-white;
                            @include font-family('Nexa-light.otf');
                            padding-top: 10px;
                        }
                    }
                    @media(max-width:991px) {
                        .links,
                        a {
                            display: block!important;
                        }
                        .col-md-12 {
                            margin-bottom: 50px!important;
                        }
                    }
                    @media(max-width:767px) {
                        .col-md-5.last {
                            margin-top: 30px;
                            &::before {
                                top: -10px!important;
                            }
                        }
                    }
                }
                
                // key frames
                @keyframes left {
                    0% {
                        opacity: 0;
                        transform: translateX(-100px);
                    }
                    80% {
                        transform: translateX(10px);
                    }
                    100% {
                        opacity: 1;
                        transform: translateX(0);
                    }
                }
                
                @keyframes right {
                    0% {
                        opacity: 0;
                        transform: translateX(100px);
                    }
                    80% {
                        transform: translateX(-10px);
                    }
                    100% {
                        opacity: 1;
                        transform: translateX(0);
                    }
                }
                
                @keyframes bottom {
                    0% {
                        transform: translateY(30px);
                        opacity: 1;
                    }
                    80% {
                        transform: translateY(10px);
                    }
                    100% {
                        transform: translateY(0);
                        opacity: 1;
                    }
                }
                
                html.active {
                    overflow: hidden;
                }
                
                //   keyframes